import React from "react";
import s from "./Navbar.module.css";
import { NavLink } from "react-router-dom";

const Navbar = () => {
  return (
    <nav className={s.nav}>
      <div className={s.item}>
        <NavLink
          to="/profile"
          /*activeClassName={s.activeLink}*/
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          Profile
        </NavLink>
      </div>
      <div className={`${s.item} ${s.active}`}>
        <NavLink
          to="/dialogs"
          activeClassName={s.activeLink}
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          Messegers
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/users"
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          Users
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/news"
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          News
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/music"
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          Music
        </NavLink>
      </div>
      <div className={s.item}>
        <NavLink
          to="/settings"
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          Settings
        </NavLink>
      </div>

      <div className={s.item}>
        <NavLink
          to="/sidebar"
          className={({ isActive }) =>
            isActive ? "active-link" : "inactive-link"
          }
        >
          <div className={s.friendsTitle}>Friends</div>
        </NavLink>
      </div>
    </nav>
  );
};

export default Navbar;

