const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGES: {
      return {
        ...state,
        messages: [
          ...state.messages,
          { id: 7, messege: action.newMessageBody },
        ],
      };
    }
    default:
      return state;
  }
};

export const addMessagesActionCreator = (newMessageBody) => ({
  type: ADD_MESSAGES,
  newMessageBody,
});

export default dialogsReducer;
